@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #0c1821;
  color: aliceblue;
  font-family: 'VT323', monospace;
  font-size: 18px;
}

main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.header {
  align-items: center;
  background-color: #d32b2b;
  border-radius: 0 0 10px 10px;
  color: aliceblue;
  display: flex;
  /* flex-direction: column; */
  /* height: 200px; */
  justify-content: space-evenly;
  min-height: 5vh;
}

.section-links {
  display: flex;
}

.nav-links {
  background-color: #a80000;
  border-radius: 0 0 10px 10px;
  display: flex;
  height: 7vh;
  padding: 1vh 0;
  justify-content: space-around;
}

.link {
  align-items: center;
  background-color: #0c1821;
  border-radius: 10px;
  color: aliceblue;
  display: flex;
  font-size: 1.5em;
  justify-content: center;
  padding: 15px 20px;
  text-decoration: none;
  transition: 1s;
  width: 10%;
}

.link:hover {
  background: #0c1821 url(/pokemon-world/static/media/pokeball.b7cd3e0a.gif) repeat center;
  background-size: 20px;
}

.link.pokemon-logo:hover {
  background-color: #333333
}

.link.pokemon-logo {
  background: #0c1821 url(/pokemon-world/static/media/pokemon-logo-pixel.032090d5.png) no-repeat center;
  background-size: 15vh;
  min-width: 110px;
}

.main-home {
  background-color: inherit ;
  background-size: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.img-container {
  margin-top: 5vh;
  position: relative;
}

.professor-img {
  height: 50vh;
}

.pokemon-image {
  max-width: 200px;
  width: 15vw;
}

.pokemon-top-right {
  display: flex;
  left: 13vw;
  position: absolute;
  top: 5vh;
}

.pokemon-top-left {
  display: flex;
  right: 13vw;
  position: absolute;
  top: 5vh;
}

.pokemon-bottom-right {
  display: flex;
  left: 13vw;
  position: absolute;
  top: 30vh;
}

.pokemon-bottom-left {
  display: flex;
  right: 13vw;
  position: absolute;
  top: 30vh;
}

.welcome-text {
  color: #FFFFFF;
  font-size: 1.3em;
  text-align: center;
}

/* Mobile */
@media only screen and (max-width: 767px) {
  .pokemon-image {
    max-width: 50px;
  }

  .pokemon-top-left {
    top: 79vh;
    right: 2vw;
    left: 0;
  }

  .pokemon-top-right {
    top: 72vh;
    left: 0;
    right: 0;
  }

  .pokemon-bottom-right {
    top: 84vh;
    left: 0;
    right: 0;
  }

  .pokemon-bottom-left {
    top: 89vh;
    left: 0;
    right: 0;
  }
}

/* Tablet */
@media only screen and (min-width: 768px) and (max-width: 1040px) {
  .pokemon-image {
    max-width: 80px;
  }

  .pokemon-top-left {
    top: 70vh;
    right: 2vw;
    left: 0;
  }

  .pokemon-top-right {
    top: 60vh;
    left: 0;
    right: 0;
  }

  .pokemon-bottom-right {
    top: 80vh;
    left: 0;
    right: 0;
  }

  .pokemon-bottom-left {
    top: 87vh;
    left: 0;
    right: 0;
  }
}

footer {
  align-items: center;
  background-color: #a80000;
  border-radius: 5px 5px 0 0 ;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  margin-top: 1vh;
}

footer > .contact-container {
  display: flex;
  flex-flow: column wrap;
  text-align: center;
  width: 80vw;
}

.contact-container > h3 { margin: 5px 0; }

.links-container {
  display: flex;
  justify-content: space-around;
  list-style: none;
}

.poke-card {
  align-items: center;
  border: 1px solid #333333;
  border-radius: 20px;
  display: flex;
  height: 200px;
  justify-content: center;
  margin: 15px;
  max-width: 440px;
  min-width: 320px;
  overflow: hidden;
  position: relative;
  width: 30vw;
}

.poke-card .pokemon-sprite {
  flex: 0.3 1 auto;
}

.poke-card .background {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.6;
    width: 100%;
    height: 100%;
}

.poke-card .info-container {
  align-items: center;
  background-color: #33333391;
  border-radius: 20px;
  color: #ced4da;
  display: flex;
  flex-flow: column nowrap;
  height: 80%;
  justify-content: space-around;
  padding: 5px;
}

.poke-card .pokemon-type {
  border-radius: 5px;
  color: #333333;
  margin-left: 5px;
  padding: 5px;
}

/* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
.flip-card {
  background-color: transparent;
  height: 80%;
  flex: 1 1 auto;
  margin-right: 20px;
  -webkit-perspective: 1000px;
          perspective: 1000px; /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  margin: 10px 0;
  transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  transition: transform 0.6s, -webkit-transform 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.flip-card:hover .flip-card-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.flip-card-back {
  align-items: center;
  background-color: #33333391;
  border-radius: 20px;
  color: #ced4da;
  display: flex;
  flex: 1 1 auto;
  flex-flow: row wrap;
  height: 80%;
  justify-content: space-around;
  margin-right: 5px;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  padding: 5px;
}

.pokemon-stats {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
}

.pokemon-moves {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  padding: 20px;
}

.pokemon-moves .pokemon-moves-container {
  display: flex;
  flex-flow: column wrap;
}

@media only screen and (min-width: 320px) and (max-width: 1250px) {
  .flip-card-back {
    flex-flow: column wrap;
    overflow-x: scroll;
    overflow-y: hidden;
  }
}

.pokedex-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pokedex {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  width: 80vw;
}

.loading-gif {
  max-width: 600px;
  width: 50vw;
}

.button-panel {
  display: flex;
  justify-content: space-around;
  width: 80vw;
}

.button-panel button {
  background-color: #a80000;
  border: none;
  border-radius: 10px;
  color: #FFFFFF;
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  margin: 0 5px;
  min-height: 40px;
  padding: 10px 20px;
  width: 150px;
}

.previous-button:disabled {
  display: none;
}

.previous-button:hover {
  background: #a80000 url(/pokemon-world/static/media/loading.176efa05.gif) repeat center;
  background-size: 45px;
  font-size: 0;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

.next-button:hover {
  background: #a80000 url(/pokemon-world/static/media/loading.176efa05.gif) repeat center;
  background-size: 45px;
  font-size: 0;
}
.search-main {
  align-items: center;
  color: inherit;
  display: flex;
}

.search-section {
  align-items: center;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  margin: 10px 0;
  width: 80vw;
}

.search-main .search-input {
  border: none;
  border-radius: 10px;
  font-family: inherit;
  font-size: inherit;
  min-width: 150px;
  outline: none;
  padding: 10px;
  width: 20vw;
}

.search-main .search-btn {
  background-color: #a80000;
  border: none;
  border-radius: 10px;
  color: #FFFFFF;
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  margin: 10px;
  min-height: 40px;
  padding: 10px 20px;
  transition: 0.5s;
  width: 150px;
}

.search-main .search-btn:hover {
  background-color: #801111;
}

.search-main .poke-card {
  min-width: 350px;
  min-height: 250px;
}

.feedback-gif {
  border-radius: 5px;
  margin-bottom: 8px;
  min-width: 250px;
  width: 30vw;
}

.no-match-content {
  height: 100vh;
}

.no-match-text {
  color: #FFFFFF;
  font-size: 1.3em;
  margin-top: 10px;
  text-align: center;
}

.no-match-image {
  height: 40vh;
  margin: 40px auto;
  width: auto;
}

